jQuery.datetimepicker.setLocale(Languages.current);
$('#dateofbirth,#passportvalidity,#starttime,#stoptime').each(function (i, elem) {
    $(elem).datetimepicker({
        timepicker: false,
        //closeOnDateSelect: true,
        format: 'Y-m-d'
    });
});




window['getLetterFill'] = function () {

    let param =$('#form').fieldsToJson({
        countryname: {
            require: true,
        },
        username: {
            require: true,
        },
        position: {
            require: true,
        },
        gender: {
            require: true,
        },
        dateofbirth: {
            require: true,
        },
        nationality: {
            require: true,
        },
        passportnumber: {
            require: true,
        },
        passportvalidity: {
            require: true,
        },
        employer: {
            require: true,
        },
        starttime: {
            require: true,
        },
        stoptime: {
            require: true,
        },
    });

    return param;

};

